/* CSS Variables */
:root {
  /* Fonts */

  /* TODO: Use separate fallback system font stacks for different fonts. Recommended source: Modern Font Stacks */

  --system-font-stack-serif: "Iowan Old Style", "Apple Garamond", Baskerville, "Times New Roman", "Droid Serif", Times, "Source Serif Pro", serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

  --logo-font: "Italianno", var(--system-font-stack-serif);
  --title-font: "Playfair Display", var(--system-font-stack-serif);
  --text-font: "Cormorant", var(--system-font-stack-serif);

  /* Colors */

  --background-color: linear-gradient(135deg, #1A1A1A 0%, #2C1810 25%, #3D2418 50%, #2C1810 75%, #1A1A1A 100%);;
  --foreground-color: #D4AF37;
  --text-color: #E0D4B8;

  /* Used in bottom borders of tab buttons on hover. */
  --foreground-color-transparent: #D4AD3780;
  --text-color-transparent: #e0d4b84d;
}

/* (0) General Styles*/

*, *::before, *::after {
  box-sizing: border-box;

  margin: 0;
  padding: 0;
}

*:focus, *:focus-visible {
  outline: none;
}

::selection {
  background-color: var(--foreground-color-transparent);
}

.colored::selection {
  background-color: var(--text-color-transparent);
}

a:hover, a:focus-visible, button:hover, button:focus-visible, #logo:hover {
  text-shadow: 0 0 8px var(--foreground-color);
}

button {
  appearance: none;
  border: none;

  cursor: pointer;
}

b {
  /* The bring attention to element (\`b\`) is not required to be rendered in bold as per the specification. */
  font-weight: bold;
}

i {
  /* The idiomatic text element (\`i\`) is not required to be rendered in italics as per the specification. */
  font-style: italic;
}

.colored, a.colored:visited {
  color: var(--foreground-color);
}

h2, .section-title {
  font-family: var(--title-font);
  font-size: 3rem;

  margin-bottom: 24px;
}

.section-content > * {
  margin-bottom: 20px;
}

.section-content > :last-child {
  margin-bottom: 48px;
}

.section-content ul, .section-content ol, .section-content dl {
  padding-top: 16px;

  padding-left: 64px;
}

body {
  display: grid;
  grid-template-rows: auto 1fr auto;

  place-items: center;

  height: 100vh;

  font-family: var(--text-font);
  font-size: 1.9rem;

  background-image: var(--background-color);

  /* Forces the background to be fixed to the viewport without getting cut off on scrolling (as its size is smaller than the size of the viewport) */
  background-attachment: fixed;

  color: var(--text-color);
}

/* (1) Header styles */

header {
  padding-bottom: 64px;
}

#logo {
  font-family: var(--logo-font);
  font-size: 5rem;

  margin-top: 32px;
  margin-bottom: 16px;

  cursor: pointer;
}

header > nav {
  display: flex;
  justify-content: space-evenly;
}

.tab {
  font-family: var(--logo-font);
  font-size: 2rem;

  padding: 0 5px 2px 5px;

  background-color: transparent;
}

.tab.active-tab, .tab:hover, .tab:focus-visible {
  --border-width: 4px;

  border-bottom: var(--border-width) solid var(--foreground-color);

  /* To prevent button text from shifting upwards on hover/keyboard focus. */
  padding-top: var(--border-width);
}

.tab:hover, .tab:focus-visible {
  border-bottom-color: var(--foreground-color-transparent);
}

/* (2) Content styles - Home tab */

#content {
  place-self: stretch;
}

#content > * {
  padding-left: 64px;
  padding-right: 64px;
}

#main-image-container {
  text-align: center;

  margin-bottom: 32px;
  padding: 0;
}

#headline {
  font-family: var(--title-font);
  font-size: 4rem;

  margin-bottom: 32px;
}

#main-image-container img, .item-image {
  border: 2px solid var(--foreground-color);
}

#text {
  font-size: 2rem;
}

.statement {
  margin-bottom: 20px;
}

.statement > :not(:last-child) {
  margin-bottom: 8px;
}

#punchline {
  font-weight: bold;

  font-size: 1.1em;
}

/* (3) Content Styles - Menu tab */

#menu {
  --padding-left-right: 64px;

  list-style-type: none;

  padding-left: var(--padding-left-right);
  padding-right: var(--padding-left-right);
}

.menu-item {
  --padding-top-bottom: 64px;

  display: flex;
  flex-direction: column;
}

.menu-item:not(:first-child) {
  padding-top: var(--padding-top-bottom);
}

.menu-item-parent {
  display: flex;

  gap: 64px;

  padding-bottom: var(--padding-top-bottom);
}

.menu-item:nth-child(2n + 1) > .menu-item-parent {
  flex-direction: row-reverse;
}

.menu-item:not(:last-child)::after, .reservation-section dt::after {
  content: '';

  align-self: center;

  width: 20%;
  height: 4px;

  background-color: var(--foreground-color);
}

.item-image {
  align-self: center;
  flex: 1.2;

  display: grid;
  place-content: center;

  font-size: 1.5rem;
  font-weight: bold;

  text-transform: uppercase;

  /* Force all image containers to have the same height, even when they contain no images or images of different dimensions. */
  height: 350px;

  /* Prevent the cursor from changing appearance on hovering over the "Photo unavailable" text. */
  cursor: default;
}

.item-text {
  flex: 3;

  display: flex;
  flex-direction: column;

  padding: 16px 0;
}

.item-image > img {
  width: 100%;
  height: 100%;
}

.item-description > * {
  margin-bottom: 16px;
}

.chef-name {
  margin-bottom: 48px;
}

.chef-name, .item-price {
  font-size: 2rem;
}

.item-price {
  margin-top: auto;
}

/* (4) Content styles - About tab */

.reservation-section {
  text-align: center;
}

.reservation-section > .section {
  border: 2px solid var(--foreground-color);
  padding: 32px;

  text-align: center;
}

.reservation-section p:first-child {
  margin-bottom: 64px;
}

.reservation-section dl {
  padding: 0;
}

.reservation-section dt {
  display: flex;
  flex-direction: column;

  font-weight: bold;

  padding-bottom: 20px;
}

.reservation-section dt::after {
  margin-top: 8px;

  width: 3.5ch;
}

.reservation-section dd:not(:last-child) {
  margin-bottom: 48px;
}

/* (3) Footer styles */

footer {
  width: 100%;

  margin-top: 48px;
  padding: 16px 0;
  border-top: 1px solid var(--foreground-color);

  text-align: center;

  font-size: 1.5rem;
}

/* Extra: Dish suggestion paragraph styles */
.suggest-dish-notification {
  padding: 8px 0;
  border: 2px double var(--foreground-color);
  margin-bottom: 64px;

  place-self: center;

  font-size: 1.5rem;

  width: fit-content;

  background-color: rgba(200, 200, 210, 0.06);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,UAAU;;AAEV,0BAA0B;AAC1B;EACE,sBAAsB;EACtB,wBAAwB;;EAExB;;8DAEgF;;EAEhF,mBAAmB;EACnB,kBAAkB;;EAElB,mBAAmB;AACrB;;AAEA,oCAAoC;AACpC;EACE,0BAA0B;EAC1B,+BAA+B;;EAE/B;;8DAE0F;;EAE1F,iBAAiB;EACjB,kBAAkB;;EAElB,kBAAkB;AACpB;;AAEA,+BAA+B;;AAE/B;EACE,qBAAqB;EACrB,wBAAwB;;EAExB;;8DAE+E;;EAE/E,gBAAgB;EAChB,kBAAkB;;EAElB,kBAAkB;AACpB;;AAEA;EACE,4BAA4B;EAC5B,wBAAwB;;EAExB;;8DAEqF;;EAErF,gBAAgB;EAChB,kBAAkB;;EAElB,kBAAkB;AACpB;;AAEA;EACE,mBAAmB;EACnB,wBAAwB;;EAExB;;8DAE6E;;EAE7E,iBAAiB;EACjB,kBAAkB;;EAElB,kBAAkB;AACpB;;AAEA,kBAAkB;AAClB;EACE,UAAU;;EAEV,+GAA+G;;EAE/G,yMAAyM;;EAEzM,wDAAwD;EACxD,gEAAgE;EAChE,wDAAwD;;EAExD,WAAW;;EAEX,4GAA4G;EAC5G,2BAA2B;EAC3B,qBAAqB;;EAErB,oDAAoD;EACpD,yCAAyC;EACzC,mCAAmC;AACrC;;AAEA,sBAAsB;;AAEtB;EACE,sBAAsB;;EAEtB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,qDAAqD;AACvD;;AAEA;EACE,+CAA+C;AACjD;;AAEA;EACE,4CAA4C;AAC9C;;AAEA;EACE,gBAAgB;EAChB,YAAY;;EAEZ,eAAe;AACjB;;AAEA;EACE,0GAA0G;EAC1G,iBAAiB;AACnB;;AAEA;EACE,yGAAyG;EACzG,kBAAkB;AACpB;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,8BAA8B;EAC9B,eAAe;;EAEf,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;;EAEjB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,iCAAiC;;EAEjC,mBAAmB;;EAEnB,aAAa;;EAEb,6BAA6B;EAC7B,iBAAiB;;EAEjB,yCAAyC;;EAEzC,kJAAkJ;EAClJ,4BAA4B;;EAE5B,wBAAwB;AAC1B;;AAEA,sBAAsB;;AAEtB;EACE,oBAAoB;AACtB;;AAEA;EACE,6BAA6B;EAC7B,eAAe;;EAEf,gBAAgB;EAChB,mBAAmB;;EAEnB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,6BAA6B;AAC/B;;AAEA;EACE,6BAA6B;EAC7B,eAAe;;EAEf,sBAAsB;;EAEtB,6BAA6B;AAC/B;;AAEA;EACE,mBAAmB;;EAEnB,gEAAgE;;EAEhE,0EAA0E;EAC1E,gCAAgC;AAClC;;AAEA;EACE,wDAAwD;AAC1D;;AAEA,kCAAkC;;AAElC;EACE,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;;EAElB,mBAAmB;EACnB,UAAU;AACZ;;AAEA;EACE,8BAA8B;EAC9B,eAAe;;EAEf,mBAAmB;AACrB;;AAEA;EACE,yCAAyC;AAC3C;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,iBAAiB;;EAEjB,gBAAgB;AAClB;;AAEA,kCAAkC;;AAElC;EACE,0BAA0B;;EAE1B