  --foreground-color-transparent: #D4AD3780;\n  --text-color-transparent: #e0d4b84d;\n}\n\n/* (0) General Styles*/\n\n*, *::before, *::after {\n  box-sizing: border-box;\n\n  margin: 0;\n  padding: 0;\n}\n\n*:focus, *:focus-visible {\n  outline: none;\n}\n\n::selection {\n  background-color: var(--foreground-color-transparent);\n}\n\n.colored::selection {\n  background-color: var(--text-color-transparent);\n}\n\na:hover, a:focus-visible, button:hover, button:focus-visible, #logo:hover {\n  text-shadow: 0 0 8px var(--foreground-color);\n}\n\nbutton {\n  appearance: none;\n  border: none;\n\n  cursor: pointer;\n}\n\nb {\n  /* The bring attention to element (`b`) is not required to be rendered in bold as per the specification. */\n  font-weight: bold;\n}\n\ni {\n  /* The idiomatic text element (`i`) is not required to be rendered in italics as per the specification. */\n  font-style: italic;\n}\n\n.colored, a.colored:visited {\n  color: var(--foreground-color);\n}\n\nh2, .section-title {\n  font-family: var(--title-font);\n  font-size: 3rem;\n\n  margin-bottom: 24px;\n}\n\n.section-content > * {\n  margin-bottom: 20px;\n}\n\n.section-content > :last-child {\n  margin-bottom: 48px;\n}\n\n.section-content ul, .section-content ol, .section-content dl {\n  padding-top: 16px;\n\n  padding-left: 64px;\n}\n\nbody {\n  display: grid;\n  grid-template-rows: auto 1fr auto;\n\n  place-items: center;\n\n  height: 100vh;\n\n  font-family: var(--text-font);\n  font-size: 1.9rem;\n\n  background-image: var(--background-color);\n\n  /* Forces the background to be fixed to the viewport without getting cut off on scrolling (as its size is smaller than the size of the viewport) */\n  background-attachment: fixed;\n\n  color: var(--text-color);\n}\n\n/* (1) Header styles */\n\nheader {\n  padding-bottom: 64px;\n}\n\n#logo {\n  font-family: var(--logo-font);\n  font-size: 5rem;\n\n  margin-top: 32px;\n  margin-bottom: 16px;\n\n  cursor: pointer;\n}\n\nheader > nav {\n  display: flex;\n  justify-content: space-evenly;\n}\n\n.tab {\n  font-family: var(--logo-font);\n  font-size: 2rem;\n\n  padding: 0 5px 2px 5px;\n\n  background-color: transparent;\n}\n\n.tab.active-tab, .tab:hover, .tab:focus-visible {\n  --border-width: 4px;\n\n  border-bottom: var(--border-width) solid var(--foreground-color);\n\n  /* To prevent button text from shifting upwards on hover/keyboard focus. */\n  padding-top: var(--border-width);\n}\n\n.tab:hover, .tab:focus-visible {\n  border-bottom-color: var(--foreground-color-transparent);\n}\n\n/* (2) Content styles - Home tab */\n\n#content {\n  place-self: stretch;\n}\n\n#content > * {\n  padding-left: 64px;\n  padding-right: 64px;\n}\n\n#main-image-container {\n  text-align: center;\n\n  margin-bottom: 32px;\n  padding: 0;\n}\n\n#headline {\n  font-family: var(--title-font);\n  font-size: 4rem;\n\n  margin-bottom: 32px;\n}\n\n#main-image-container img, .item-image {\n  border: 2px solid var(--foreground-color);\n}\n\n#text {\n  font-size: 2rem;\n}\n\n.statement {\n  margin-bottom: 20px;\n}\n\n.statement > :not(:last-child) {\n  margin-bottom: 8px;\n}\n\n#punchline {\n  font-weight: bold;\n\n  font-size: 1.1em;\n}\n\n/* (3) Content Styles - Menu tab */\n\n#menu {\n  --padding-left-right: 64px;\n\n  list-style-type: none;\n\n  padding-left: var(--padding-left-right);\n  padding-right: var(--padding-left-right);\n}\n\n.menu-item {\n  --padding-top-bottom: 64px;\n\n  display: flex;\n  flex-direction: column;\n}\n\n.menu-item:not(:first-child) {\n  padding-top: var(--padding-top-bottom);\n}\n\n.menu-item-parent {\n  display: flex;\n\n  gap: 64px;\n\n  padding-bottom: var(--padding-top-bottom);\n}\n\n.menu-item:nth-child(2n + 1) > .menu-item-parent {\n  flex-direction: row-reverse;\n}\n\n.menu-item:not(:last-child)::after, .reservation-section dt::after {\n  content: '';\n\n  align-self: center;\n\n  width: 20%;\n  height: 4px;\n\n  background-color: var(--foreground-color);\n}\n\n.item-image {\n  align-self: center;\n  flex: 1.2;\n\n  display: grid;\n  place-content: center;\n\n  font-size: 1.5rem;\n  font-weight: bold;\n\n  text-transform: uppercase;\n\n  /* Force all image containers to have the same height, even when they contain no images or images of different dimensions. */\n  height: 350px;\n\n  /* Prevent the cursor from changing appearance on hovering over the \"Photo unavailable\" text. */\n  cursor: default;\n}\n\n.item-text {\n  flex: 3;\n\n  display: flex;\n  flex-direction: column;\n\n  padding: 16px 0;\n}\n\n.item-image > img {\n  width: 100%;\n  height: 100%;\n}\n\n.item-description > * {\n  margin-bottom: 16px;\n}\n\n.chef-name {\n  margin-bottom: 48px;\n}\n\n.chef-name, .item-price {\n  font-size: 2rem;\n}\n\n.item-price {\n  margin-top: auto;\n}\n\n/* (4) Content styles - About tab */\n\n.reservation-section {\n  text-align: center;\n}\n\n.reservation-section > .section {\n  border: 2px solid var(--foreground-color);\n  padding: 32px;\n\n  text-align: center;\n}\n\n.reservation-section p:first-child {\n  margin-bottom: 64px;\n}\n\n.reservation-section dl {\n  padding: 0;\n}\n\n.reservation-section dt {\n  display: flex;\n  flex-direction: column;\n\n  font-weight: bold;\n\n  padding-bottom: 20px;\n}\n\n.reservation-section dt::after {\n  margin-top: 8px;\n\n  width: 3.5ch;\n}\n\n.reservation-section dd:not(:last-child) {\n  margin-bottom: 48px;\n}\n\n/* (3) Footer styles */\n\nfooter {\n  width: 100%;\n\n  margin-top: 48px;\n  padding: 16px 0;\n  border-top: 1px solid var(--foreground-color);\n\n  text-align: center;\n\n  font-size: 1.5rem;\n}\n\n/* Extra: Dish suggestion paragraph styles */\n.suggest-dish-notification {\n  padding: 8px 0;\n  border: 2px double var(--foreground-color);\n  margin-bottom: 64px;\n\n  place-self: center;\n\n  font-size: 1.5rem;\n\n  width: fit-content;\n\n  background-color: rgba(200, 200, 210, 0.06);\n}"],"sourceRoot":""}]);
